@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {

  font-family: 'Montserrat', sans-serif;

}

html {

  background-color: #e8ebf4;

}

body {

  width: 100%;
  height: 100%;

}

/* ---------------------| RESPOSIVE  |---------------------*/

@media screen and (max-width: 425px) {

header.App-header {

  display: grid;
  height: 150px;
  background: #111e6c;

}

img.App-logo {

  margin: 3px 55px auto;

}

main.Main {

  display:grid;
  margin: 0px;

  }

div.Main-card {
  width: 200px;
  margin: 20px;

}

div.Main-title {
  width: 200px;
}

.Main-title {

  height: 40px;

}

h3.Main-card__text {
  font-size: 14px;
}

div.Main-buttons__list {
  display: contents;
}

div.table-title {

  width: 230px;
  justify-content: space-evenly;
  font-size: 14px;
  margin: 10px;

}

div.Main-buttons__filter {

  display: block;

}

table.table {

  width: 250px;
  font-size: 14px;

}

}

@media screen and (max-width: 768px) {

header.App-header {
  justify-content: space-around;
}
  
  }
.App-logo {

  height: 70px;
  margin-left: 50px;

}

.App-header {

  height: 80px;
  background: linear-gradient(90deg, rgba(17,30,108,1) 17%, rgba(47,35,104,1) 39%, rgba(62,38,102,1) 51%, rgba(74,40,100,1) 69%, rgba(239,67,78,1) 100%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: calc(5px + 2vmin);

}

.Nav-header {

  align-items: center;
  display: flex;
  margin-right: 50px;

}

.Nav-header ul {

  display: flex;
  align-items: center;
  list-style: none;

}

.Nav-header li a {

  text-decoration: none;
  margin-right: 20px;
  color: white;

}

.icon {

  height: 20px;
  width: 20px;

}

.Nav-ayuda {

  display: flex;
  align-items: center;
  margin-right: 20px;

}

.Nav-ayuda a {

  display: flex;
  align-items: center;

}


/* RESPONSIVE  */


.Main {

    display: flex;
    height: 100%;
    width: 90%;
    margin: 0 auto;
    padding-top: 30px;
    background-color: #e8ebf4;

}

.Main-card {

    display: grid;
    height: 200px;
    width: 30%;
    border-radius: 10px;
    background-color: white;

}

.Main-title {

    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-around;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background: linear-gradient(90deg, rgba(74,40,100,1) 0%, rgba(203,61,83,1) 100%, rgba(239,67,78,1) 100%);

}

.Main-card__icon {

    width: 30px;
    height: 30px;
    margin-left: 5px;

}

.Main-card__text {

    color: white;

}

.Main-card__dates {

    display: grid;
    justify-content: center;
    align-items: center;
    padding: 0px;

}

.Main-card__cash {

    display: flex;
    justify-content: center;
    font-size: 25px;
    color:  rgb(74,40,100);

}

.Main-card__date {
    
    display: flex;
    font-size: small;
    justify-content: center;
    margin-top: -2px;
    color: #111e6c;

}

.Main-buttons {

    width: 70%;
    margin-left: 50px;

}

.Main-buttons__list {

    display: flex;
    height: 40px;
    justify-content: space-around;
    align-items: center;
    background-color: white;

}

.Main-buttons__list button {

    height: 25px;
    width: 150px;
    background-color: white;
    border-radius: 10px;
    font-size: 15px;
    border: transparent;
    cursor: pointer;
    color: #111e6c;

}

.Main-buttons__list button:hover {

    background-color: #969696;

}

.Main-buttons__filter {

    display: flex;
    height: 40px;
    margin-top: 10px;
    justify-content: flex-end;

}

.Main-buttons__filter button {

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin: 0px;
    width: 150px;
    background-color: white;
    border: transparent;
    cursor: pointer;

}

.filter {

    margin-left: 5px;
    height: 20px;
    width: 20px;

}
.table {

    width: 90%;
    margin: 0 auto;
    border: 1px solid #969696;

}

.table-files th {

    color: #323232;
    border-bottom: 1px solid #969696;
    padding-bottom: 10px;

}

.table-columns td {

    border-bottom: 1px solid #969696;

}

.table tr {

    height: 55px;

}

.table-columns td p {

    display: flex;
    justify-content: center;

}

.table-columns3 td p {

    display: flex;
    justify-content: center;

}

.table-columns3 {

    border-bottom: 1px solid #969696;

}

.table-title {

    display: flex;
    background: linear-gradient(90deg, rgba(17,30,108,1) 17%, rgba(47,35,104,1) 39%, rgba(62,38,102,1) 51%, rgba(74,40,100,1) 69%, rgba(239,67,78,1) 100%);
    height: 60px;
    width: 90%;
    margin: 0 auto;
    margin-top: 30px;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

}

.table-title h3 {

    margin-left: 10px;
    color: white;

}

.icon-cobro, .icono-metodo {

    width: 20px;
    height: 20px;

}

.table-transaccion, .table-metodo {

    display: flex;
    justify-content: center;
    align-items: center;

}

.table-transaccion img {

    margin-right: 5px;

}

.table-metodo img {

    margin-right: 5px;

}
